<template>
  <!-- event created modal -->
  <v-card class="fail__modal">
    <!-- svg icon to be used here -->
    <v-layout wrap justify-center class="event__fail">
      <v-flex sm12 class="flex-center">
        <v-card-title>
          <h1>Error !!</h1>
        </v-card-title>
      </v-flex>
  
      <v-flex sm12 class="flex-center">
        <v-card-text>
          <h2>{{error}}</h2>
        </v-card-text>
      </v-flex>

      <v-card-actions>
        <v-btn color="primary" round @click="close">
          Close
        </v-btn>
      </v-card-actions>
    </v-layout>
  
  
  </v-card>
</template>

<script>
export default {
  props: {
    error: {
      type: String,
      required: true
    }
  },
  methods: {
    close: function() {
      this.$emit("clicked");
    }
  }
};
</script>


<style lang="scss" scoped>
.fail__modal {
  background: linear-gradient(
    135deg,
    rgba(0, 0, 255, 0.4),
    rgba(255, 0, 0, 0.3)
  );
}

.flex-center {
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
}

h2 {
  text-align: center;
}
</style>



